*{
  font-family: 'Courier New', Courier, monospace;
  font-size: 1.5 rem;
}
body{
  padding: 60px;
}

input{
  color: blue;
  font-size: 1.2rem;
  width: 100%;
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-left: 10px;
  overflow: hidden; /*para q los bordes de las imagenes no se salgan del cotenedor*/


}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}

.card-grid{ /*flex es parea que se acomode en una linea*/
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}